import { FC } from 'react'
import PopupHelp from './PopupHelp.js'

/**
 * Simplified CheckboxWithLabel component for Ziggy plugins
 */

export interface CheckboxWithLabelProps {
  label: string
  size?: number
  className?: string
  checkboxClassName?: string
  checked: boolean
  onChange: () => void
  popupHelp?: string
  disabled?: boolean
}

const CheckboxWithLabel: FC<CheckboxWithLabelProps> = ({
  label,
  size = 25,
  className,
  checkboxClassName,
  checked,
  onChange,
  popupHelp,
  disabled = false,
}) => (
  <div
    className={`flex flex-row place-items-center gap-2 ${disabled ? 'cursor-not-allowed opacity-50' : 'cursor-pointer'} ${className || ''}`}
    onClick={disabled ? undefined : onChange}
  >
    <input
      aria-label={label}
      type="checkbox"
      autoComplete="new-password"
      checked={checked}
      disabled={disabled}
      className={`ml-1 bg-hsMidGrayBg text-gray-700 p-0 ring-0 rounded-sm
        border-none outline-none ring-0 focus:ring-0 focus:outline-[0px] focus:border-transparent
        active:ring-0 active:outline-none active:border-transparent ${disabled ? 'cursor-not-allowed' : 'cursor-pointer'}
        ${checkboxClassName || ''}`}
      style={{ width: `${size}px`, height: `${size}px` }}
      onChange={() => {}}
    />
    <div className="ml-[3px] flex flex-row gap-1">
      <div className="leading-[1.1]">{label}</div>
      {popupHelp && <PopupHelp text={popupHelp} />}
    </div>
  </div>
)

export default CheckboxWithLabel
